main.select-categories{display:flex;justify-content:none;align-items:center;flex-direction:column;padding:2em clamp(.5em,3vw,2em)}main.select-categories div.wrap{width:100%}main.select-categories div.wrap h1{font-size:clamp(1.75rem,8vw,2.5rem);opacity:.25;font-weight:800;color:#b4b4b4;margin-bottom:1em}main.select-categories div.wrap section.category-select{display:flex;justify-content:space-between;align-items:none;flex-direction:row;align-items:stretch;flex-wrap:wrap}main.select-categories div.wrap section.category-select article.category{width:18%;min-width:200px;max-width:400px;flex-wrap:nowrap;margin:0 .5em;padding:1.5em 1em;margin-bottom:1em;background:rgba(109,160,255,.4);border-radius:.2em;display:flex;justify-content:none;align-items:center;flex-direction:column;cursor:pointer;border:2px solid rgba(0,0,0,0)}main.select-categories div.wrap section.category-select article.category p{text-align:center;pointer-events:none}main.select-categories div.wrap section.category-select article.category p.name{font-weight:700;font-family:"Roboto Slab",serif;margin:.25em 0 1em 0;font-size:clamp(0.9rem, 4vw, 1rem)}main.select-categories div.wrap section.category-select article.category p.total{font-size:.8rem;color:#6da0ff;font-weight:500}main.select-categories div.wrap section.category-select article.category span{pointer-events:none;display:block;height:clamp(2rem,15vw,3rem);width:clamp(2rem,15vw,3rem)}main.select-categories div.wrap section.category-select article.category.active{pointer-events:none;border-color:#adff2f}main.select-categories div.wrap button{padding:.75em 2em;border:0;outline:0;background:#3155be;transition:.3s;margin-top:2em;margin-left:.5em;cursor:pointer;border-radius:.25em}main.select-categories div.wrap button label{color:#f5f5f5}main.select-categories div.wrap button:hover{background:#4169e1}@media(max-width: 480px){main.select-categories{padding:2em .25em}main.select-categories div.wrap h1{text-align:center}main.select-categories div.wrap section.category-select article.category{min-width:auto;max-width:none;width:46%;margin:0 .25em;padding:.5em;margin-bottom:.75em}}